import { LogOut } from '@/api/auth'
import { Search, User } from 'lucide-react'
import Image from 'next/image'
import Link from 'next/link'
import React from 'react'

const Header = () => {

  const useApp


  return (
    <div className=" shadow-borderShadow border-b-[1px] border-borderColor">
    <div className='py-2 flex items-center justify-between  _container'>
      <div className="basis-[480px] flex justify-between">
        <div>
          <Link className='basis-[150px] flex gap-2 items-center group font-bold' href={"/"}>
            <Image className='transition-transform group-hover:rotate-90' width={40} height={40} src="logo.svg" alt='logo'/>
            <span className='default-link group:hover text-xl'>Foodie Hub</span>
          </Link>
        </div>
        <nav className='basis-[180px] flex gap-[18px] items-center'>
          <Link className='hover:text-primary font-bold' href={"#"}>Home</Link>
          <Link className='hover:text-primary font-bold' href={"#"}>Profile</Link>
          <Link className='hover:text-primary font-bold' href={"#"}>Orders</Link>
        </nav>
      </div>
      <div className="flex gap-3 items-center"> {/* flex items-center для вертикального центрирування */}
        <div className="relative w-[270px]"> {/* задаємо ширину для input */}
          <Search className='absolute left-2.5 top-2' />
          <input placeholder='Search for restaurants or dishes...'
            type="text" 
            className='leading-[22px] pl-[38px] h-[40px] pr-3  text-sm input' 
          />
        </div>
        <div className="rounded-full relative p-3 border-borderColor border-[2px] ">
          <User />
          <div className="min-w-[150px] flex flex-col top-full right-0 absolute border-borderColor mt-1  bg-primary  p-1 border-[1px] rounded-[6px]">
            <Link href="/profile" className="text-white text-sm font-semibold transition-all hover:opacity-65">Profile</Link>
            <button onClick={()=>{
              LogOut();
              dispatch()
            }} className="text-white text-sm font-semibold transition-all hover:opacity-65">Log out</button>
            <div className="">dasd</div>
            <div className="">dasd</div>
          </div>
        </div>
      </div>
    </div>
    </div>
  )
}

export default Header;
